import Vue from 'vue';
import Router from 'vue-router';
import Main from '@/components/Main';
import kitchenDetails from '@/components/kitchenDetails';
import fullPortfolio from '@/components/fullPortfolio';
import category from '@/components/category';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Main',
      component: Main
    },
    {
      path: '/kitchenDetails',
      name: 'kitchenDetails',
      component: kitchenDetails
    },
    {
      path: '/portfolio',
      name: 'fullPortfolio',
      component: fullPortfolio
    },
    {
      path: '/category',
      name: 'category',
      component: category
    },

  ]
});
